<template>
  <div class="button">
    <button
      class="button-class"
      @click="onClick"
    >
      Click on me
    </button>
  </div>
</template>

<script>
  export default {
    name: 'AwesomeButton',

    methods: {
      onClick() {
        this.$emit('click');
      },
    },

  };
</script>

<style scoped>
  .button-class {
      color: #fff;
      background-color: green;
      padding: 5px;
      border-radius: 5px;
    }
</style>
