<template>
  <div
    id="app"
    class="application"
  >
    <AwesomeButton @click="changeTheme" />
    <AwesomeButton
      @click="loginUser"
    />
    Is user logged in: {{ isUserLoggedIn }}
    <div v-if="errorCode">
      {{ errorCode }}
    </div>
    <router-view />
    <div
      class="logos"
      :style="{ backgroundColor: style }"
    >
      <img
        alt="Vue logo"
        width="300"
        height="300"
        src="../public/logo.svg"
      />
    </div>
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';
  import AwesomeButton from './components/AwesomeButton/AwesomeButton.vue';

  export default {
    name: 'App',

    components: {
      AwesomeButton,
    },

    data() {
      return {
        style: '#fff',
      };
    },

    computed: {
      ...mapGetters('LoginModule', [
        'isUserLoggedIn',
        'errorCode',
      ]),
    },

    methods: {
      ...mapActions('LoginModule', ['loginUser']),
      changeTheme() {
        if (this.style === '#fff') {
          this.style = 'rgb(26 28 29)';
        } else {
          this.style = '#fff';
        }
      },
    },
  };
</script>

<style scoped>
.application {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /*background-color: cadetblue;*/
}

.logos {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
</style>
