import Vue from 'vue';
import VueRouter from 'vue-router';

import PageOne from 'Pages/PageOne.vue';
import PageTwo from 'Pages/PageTwo.vue';

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: PageOne,
      name: 'PageOne',
    },
    {
      path: '/page-two',
      component: PageTwo,
      name: 'PageTwo',
    },
  ],
});

export default router;
